.btn {
  color: #FFF;
  background: linear-gradient(#5DDF5A, #4CB649); }
  .btn:focus {
    color: #FFF; }
  .btn:hover {
    color: #FFF;
    background: linear-gradient(#7AFA78, #5DDF5A); }
  .btn:active {
    background: linear-gradient(#4CB649, #40993D); }

.btn.btn-danger {
  background: #F24458; }
  .btn.btn-danger:hover {
    background: #ef142d; }
  .btn.btn-danger:active {
    background: #f57483; }

.dark .btn:disabled {
  opacity: 1;
  color: #323642;
  background: #5C6370; }

.light .btn:disabled {
  opacity: 1;
  color: #FFF;
  background: #D7D8D9; }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 100;
  src: local("Roboto Thin"), local("Roboto-Thin"), url("/fonts/roboto-normal-100-thin.ttf") format("truetype"); }

