  font-weight: 900;
  src: local("Roboto Black"), local("Roboto-Black"), url("/fonts/roboto-normal-900-black.ttf") format("truetype"); }

body {
  font-family: "Roboto", Helvetica, Arial, sans-serif; }

h1 {
  margin: 10px 0; }

.container {
  margin: auto;
  max-width: 750px; }
  .container #PIALogo {
    text-align: center;
    margin: 75px 0 100px; }
    .container #PIALogo img {
      max-width: 210px; }
  .container .left-container, .container .right-container {
    float: left;
    width: 50%;
    padding: 0px; }
  .container .left-container {
    text-align: center; }
    .container .left-container img {
      max-width: 300px;
      margin-right: 50px; }
  .container .right-container {
    text-align: left; }
    .container .right-container #title {
      font-size: 2.75em;
      margin-bottom: 0px !important; }
    .container .right-container #error {
      color: #666;
      font-size: 13px;
      line-height: 2; }
    .container .right-container #message {
      color: #818181;
      font-weight: normal;
      font-size: 15px;
      line-height: 1.7; }
    .container .right-container .btn {
      font-weight: 500;
      font-size: 12pt;
      text-decoration: none;
      text-transform: uppercase;
      width: 100px;
      padding: 10px 15px; }

@media (max-width: 750px) {
  .container .left-container, .container .right-container {
    float: none;
    width: 100%;
    text-align: center; }
  .container .left-container img {
    max-width: 200px;
    margin: 0; }
  .container .right-container #message {
    padding: 0px 30px; } }
